





export default
  name: 'FlexibleImage'
  introduction: "Zoom in/out the image by centering."
  description: """
    TODO
  """
  token: '<flexible-image image-url="http://placehold.it/350x250" :width="150" :height="150"></flexible-image>'
  props:
    imageUrl:
      type: String
      required: true
      note: 'image url'
    width:
      type: Number
      required: true
      note: 'image width'
    height:
      type: Number
      required: true
      note: 'image height'
    unit:
      type: String
      required: false
      default: 'px'
      note: 'unit of image height/width'
  data: ->
    image: null
    fixedWidth: null
    fixedHeight: null
  computed:
    aspectRatio: -> @width / @height
  methods:
    loadImage: ->
      @image = new Image()
      @image.onload = ->
        @calcLength()
    calcLength: ->
      imageAspectRatio = @image.width / @image.height
      @fixedWidth =
        if @aspectRatio < imageAspectRatio
          null
        else
          @width
      @fixedHeight =
        if @aspectRatio < imageAspectRatio
          @height
        else
          null
  mounted: ->
    @loadImage()



